.footer {
    padding: 30px 0 30px;
    background: #f95858;
}

.footer p {
    color: #fff;
    font-size: 14px;
    text-align: center;
}


@media (max-width: 768px) {

    .footer p {
        color: #fff;
        font-size: 10px;
        text-align: center;
    }

}

  /* responsive mobile */
@media (max-width: 600px) {
    .footer p {
        color: #fff;
        font-size: 10px;
        text-align: center;
    }
}
  
  /* responsive tablette */
@media (max-width: 1200px) {
    .footer {
        padding: 30px 0 30px;
        background: #f95858;
    }
    
    .footer p {
        color: #fff;
        font-size: 14px;
        text-align: center;
    }
}
  
  

