.contacter {
    padding: 80px 0;
    background: #fff;
}

.contacter .section__heading {
    margin-bottom: 50px;
    text-align: center;
}

.contact__form {
    margin: 0 auto;
    padding: 40px;
    background: #fff;
    border-radius: 10px;
    width: 600px;
    box-shadow: 0 10px 60px 0px rgb(0 0 0 / 20%);
}

.contact-form-control {
    height: 45px;
    border: #ccc solid 1px;
    margin-bottom: 30px;
}

textarea.contact-form-control {
    min-height: 100px;
}

.btn-contact-form {
    display: block;
    width: 100%;
    padding: 10px;
    font-size: 18px;
    text-transform: uppercase;
    background: linear-gradient(90deg,ss
            #f95858 0%,
            rgba(249, 115, 255, 1) 100%);
    color: #fff;
    transition: all linear 0.3s;
}

.btn-contact-form:hover {
    color: #fff;
    background: linear-gradient(90deg, rgba(251, 120, 120, 1) 0%, rgba(255, 175, 116, 1) 100%);
}

