.about{
    width: 100%;
    display: contents;
    background-color: rgb(164, 170, 180);

    position: relative;
    transform: translate(-50%, -50%);
    top:50%;
    left:50%;
    text-align: center;
}

@media screen and (max-width: 640px){
    .about h1{
        font-size: 3rem;
    }

    .about p{
        font-size: 1.4rem;
    }
}